import { Component  } from '@angular/core';
import {  ViewController, IonicPage, NavController, NavParams } from 'ionic-angular';
import { Auth } from '../../../providers';


@IonicPage()
@Component({  
  selector: 'page-detalle1',
  templateUrl: 'detalle1.html',
})
export class Detalle1Page {
  idtipo:any;
  ind1m: any=[];
  idindicador:any;
  idpolitica:any;
  idarea:any;
  opcion:any;
  tituloxx:any;
  constructor(   public regService: Auth,public viewCtrl: ViewController,  public navParams2: NavParams,public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Detalle1Page');
    this.idtipo=this.navParams.data.idtipo;
    this.idindicador=this.navParams.data.idindicador; 
    this.idpolitica=this.navParams.data.idpolitica;  
    this.idarea=this.navParams.data.idarea;  
    this.tituloxx=this.navParams.data.titulo

  this.opcion=this.navParams.data.op
    if(this.navParams.data.op==1)  
    {
      this.regService.getRegsintokora('ind1d/' + this.idindicador,'oracle2').then((data) => {
        this.ind1m=data;
      }, (err) => {   
        //this.msj.showError3('Error',err.error);   
        this.ind1m=[]
      
      });    

    }
    else{

      if(this.navParams.data.op==2)
      {

        
      this.regService.getRegsintokora('ind2d/' + this.idindicador,'oracle2').then((data) => {
        this.ind1m=data;
      
    }, (err) => {   
      //this.msj.showError3('Error',err.error);   
      this.ind1m=[]
    
    });    


      }
      else
      {

        if(this.navParams.data.op==3)
        {
             
              this.regService.getRegsintokora('participacion/' + this.idindicador,'oracle2').then((data) => {
                this.ind1m=data;
              }, (err) => {   
                this.ind1m=[]
                //this.msj.showError3('Error',err.error);   
              
              });    

        }
        else{

          if(this.navParams.data.op==5)
          {
               
                this.regService.getRegsintokora('ind1det/' + this.idindicador,'oracle2').then((data) => {
                  this.ind1m=data;
                }, (err) => {   
                  this.ind1m=[]
                  //this.msj.showError3('Error',err.error);   
                
                });    
  
          }
          else{
    
             
                  this.regService.getRegsintokora('pggpoliticapublica/' + this.idpolitica+ '/' + this.idarea,'oracle2').then((data) => {
                    this.ind1m=data;
                  }, (err) => {   
                    this.ind1m=[]
                    //this.msj.showError3('Error',err.error);   
                  
                  });    

                }

        }
     

      



      }

    }
   


  }

  
  openlink(op) {
    window.open(op,'_system', 'location=no');
  }
    

  cancel() {
    this.viewCtrl.dismiss();
  }

}



